xt {\n  font-family: \"Roboto\";\n  font-style: normal;\n  font-weight: 400;\n  font-size: 16px;\n  line-height: 140%;\n}\n\n%small_text {\n  font-family: \"Roboto\";\n  font-style: normal;\n  font-weight: 300;\n  font-size: 15px;\n  line-height: 120%;\n}\n","@import \"@styles/fonts.scss\";\n@import \"@styles/variables.scss\";\n@import \"@styles/mixins.scss\";\n@import \"@styles/placeholders.scss\";\n\n.button {\n  width: 298px;\n  min-height: 54px;\n\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  background-color: $orang;\n  color: $white;\n  text-transform: uppercase;\n\n  @extend %buttons_text;\n\n  &:hover {\n    background-color: $orang_dark;\n  }\n\n  &:active {\n    background-color: $green;\n  }\n\n  &:disabled {\n    background-color: $gray;\n  }\n}\n\n.button--circle {\n  @include button_border_radius_mixin(25px);\n}\n\n.button--quadro {\n  @include button_border_radius_mixin(5px);\n}\n","// colors\n$green: #4b9200;\n$orang: #f9804b;\n$orang_dark: #e47209;\n$yellow: #ffee2e;\n$orang_grad: #fed290;\n$pink_grad: #febdab;\n$red: #d31414;\n$black_blue: #333b41;\n$gray: #767474;\n$gray_smoky: #ababab;\n$light_gray: #f1f3f2;\n$frosted_glass_white: #fdfdff;\n$white: #ffffff;\n$black: #000000;\n$pick_color: #fe9013;\n$footer_color: #111115;\n$input_text: #929699;\n\n$radius: 25px;\n","@mixin button_border_radius_mixin($radius) {\n  border-radius: $radius;\n}\n","@import \"@styles/variables.scss\";\n\n.nav {\n  width: 472px;\n  height: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.nav__list {\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 58px;\n}\n","@import \"@styles/variables.scss\";\n@import \"@styles/placeholders.scss\";\n\n.header {\n  height: 80px;\n  background-color: $black;\n  color: $white;\n  font-weight: 900;\n}\n\n.header__container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  height: 100%;\n}\n","