 $orang;\n\n      background-image: url(\"@icons/arrow_right-white.svg\");\n      background-repeat: no-repeat;\n      background-position: center;\n\n      &:hover {\n        background-color: $orang_dark;\n      }\n\n      &:active {\n        background-color: $green;\n      }\n\n      &:disabled {\n        background-color: $gray;\n      }\n    }\n\n    .animals-card__arrow-left {\n      position: absolute;\n      top: 403px;\n      left: -76px;\n      width: 62px;\n      height: 62px;\n      border-radius: 100%;\n      background-color: $orang;\n\n      background-image: url(\"@icons/arrow_left-white.svg\");\n      background-repeat: no-repeat;\n      background-position: center;\n\n      &:hover {\n        background-color: $orang_dark;\n      }\n\n      &:active {\n        background-color: $green;\n      }\n\n      &:disabled {\n        background-color: $gray;\n      }\n    }\n  }\n\n  .animals-card__list {\n    margin-bottom: 80px;\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    grid-template-rows: repeat(2, 1fr);\n    column-gap: 30px;\n    row-gap: 20px;\n\n    .card {\n      width: 366px;\n      height: 426px;\n      border: 1px solid $orang_dark;\n      border-radius: 5px;\n\n      .card__image {\n        width: 100%;\n        height: 366px;\n      }\n\n      .card__info {\n        position: relative;\n        margin-top: 8px;\n        margin-bottom: 14px;\n        margin-left: 16px;\n\n        h4 {\n          text-transform: uppercase;\n        }\n\n        .card__info__icon_banan {\n          position: absolute;\n          top: 2px;\n          right: 12px;\n          width: 33px;\n          height: 37px;\n          background-image: url(\"@icons/banana-bamboo_icon.svg\");\n          background-repeat: no-repeat;\n          background-size: 100%;\n        }\n\n        .card__info__icon_fish {\n          position: absolute;\n          top: 0;\n          right: 14px;\n          width: 48px;\n          height: 38px;\n          background-image: url(\"@icons/meet-fish_icon.svg\");\n          background-repeat: no-repeat;\n          background-size: 100%;\n        }\n      }\n\n      .card__image-1 {\n        background-image: url(\"@images/card_pandas.jpg\");\n        background-size: cover;\n      }\n\n      .card__image-2 {\n        background-image: url(\"@images/eagles.jpg\");\n        background-size: cover;\n      }\n\n      .card__image-3 {\n        background-image: url(\"@images/gorillas.jpg\");\n        background-size: cover;\n      }\n\n      .card__image-4 {\n        background-image: url(\"@images/sloth.jpg\");\n        background-size: cover;\n      }\n\n      .card__image-5 {\n        background-image: url(\"@images/chetahs.jpg\");\n        background-size: cover;\n      }\n\n      .card__image-6 {\n        background-image: url(\"@images/penguins.jpg\");\n        background-size: cover;\n      }\n    }\n  }\n\n  .button_animals_card {\n    margin: 0 auto;\n  }\n}\n","@import \"@styles/variables.scss\";\n\n.friend {\n  background-color: $pink_grad;\n  padding-top: 69px;\n  padding-bottom: 100px;\n\n  .friend__container {\n    padding-top: 41px;\n    padding-bottom: 59px;\n    padding-left: 99px;\n    padding-right: 61px;\n\n    height: 835px;\n    border-radius: 20px;\n\n    backdrop-filter: blur(2px);\n    background: rgba(253, 253, 255, 0.52);\n\n    h2 {\n      margin-bottom: 30px;\n      color: $pick_color;\n      text-align: center;\n    }\n\n    .friend_text_1 {\n      margin-bottom: 40px;\n    }\n\n    .friend_text_2 {\n      margin-bottom: 10px;\n    }\n\n    .friend_text_3 {\n      margin-bottom: 60px;\n\n      a {\n        text-decoration: underline;\n      }\n    }\n\n    h3 {\n      margin-bottom: 40px;\n      text-align: center;\n    }\n\n    .payment {\n      margin: 0 auto;\n      margin-bottom: 60px;\n      width: 764px;\n      height: 244px;\n\n      display: grid;\n      grid-template-columns: 168px 130px 168px 130px 168px;\n\n      .pay {\n        &:first-child {\n          .pay-image {\n            background-image: url(\"@icons/pay.svg\");\n            background-repeat: no-repeat;\n            background-position: center;\n          }\n        }\n\n        &:nth-child(3) {\n          .pay-image {\n            background-image: url(\"@icons/zoo.svg\");\n            background-repeat: no-repeat;\n            background-position: center;\n          }\n        }\n\n        &:last-child {\n          .pay-image {\n            background-image: url(\"@icons/monkey.svg\");\n            background-repeat: no-repeat;\n            background-position: center;\n          }\n        }\n      }\n\n      .pay-image {\n        margin-bottom: 30px;\n        width: 168px;\n        height: 168px;\n        border: 1px solid $orang;\n        border-radius: 5px;\n        background-color: $white;\n      }\n\n      .arrow-pay {\n        height: 168px;\n        background-image: url(\"@icons/arrow-friend.svg\");\n        background-repeat: no-repeat;\n        background-position: center;\n      }\n    }\n\n    .friend__button {\n      margin: 0 auto;\n    }\n  }\n}\n","@import \"@styles/variables.scss\";\n\n.testimonials {\n  padding-bottom: 180px;\n\n  background-image: url(\"@images/monterra.png\");\n  background-repeat: no-repeat;\n  background-position: right bottom -130px;\n}\n\n.container__testimomials {\n  padding-top: 140px;\n  height: 100%;\n\n  h3 {\n    margin-bottom: 47px;\n    @extend %h3_title;\n    text-align: center;\n  }\n\n  .testimonials__list {\n    margin-bottom: 21px;\n    display: grid;\n    grid-template-columns: repeat(4, 1fr);\n    gap: 30px;\n\n    .testimonials__item {\n      padding: 15px;\n      padding-bottom: 19px;\n      width: 268px;\n      height: 391px;\n      border: 2px solid $pink_grad;\n      border-radius: 20px;\n\n      .testimonials__item--user {\n        display: flex;\n        .testimonials-user__icon {\n          margin-right: 10px;\n          width: 37px;\n          height: 37px;\n\n          background-image: url(\"@icons/user_icon.svg\");\n          background-repeat: no-repeat;\n          background-position: center;\n        }\n\n        & .icon-2 {\n          background-image: url(\"@icons/user_icon_2.svg\");\n        }\n\n        & .icon-3 {\n          background-image: url(\"@icons/user_icon_3.svg\");\n        }\n\n        & .icon-4 {\n          background-image: url(\"@icons/user_icon_4.svg\");\n        }\n\n        .testimonials-user__info {\n          & .title {\n            margin-bottom: 1px;\n            @extend %main_text;\n            color: $black;\n          }\n\n          & .subtitle {\n            margin-bottom: 10px;\n          }\n        }\n      }\n\n      .testimonials__item--text {\n        overflow: hidden;\n        text-overflow: clip;\n        max-height: calc(100% - 65px);\n        @extend %small_text;\n      }\n    }\n  }\n\n  .line-scroll {\n    margin: 0 auto;\n    margin-bottom: 31px;\n    width: 599px;\n    height: 8px;\n    border: 0.5px solid $pink_grad;\n    border-radius: 5px;\n\n    .slider {\n      width: 115px;\n      height: 6px;\n      background-color: $pick_color;\n\n      &:hover {\n        background-color: $orang_dark;\n      }\n\n      &:active {\n        background-color: $green;\n      }\n    }\n  }\n}\n","@import \"@styles/variables.scss\";\n\n.footer {\n  height: 443px;\n  background-color: $footer_color;\n\n  .footer__container {\n    padding-top: 40px;\n    padding-bottom: 50px;\n    height: 100%;\n    display: grid;\n    grid-template-columns: 1fr 1fr 404px;\n    grid-template-rows: 100% 1fr;\n\n    .footer__social {\n      margin-right: 101px;\n      .footer__logo {\n        margin-bottom: 68px;\n      }\n\n      .footer__social--button {\n        margin-bottom: 44px;\n      }\n\n      .footer__social--list {\n        width: 120px;\n\n        .footer__social--item {\n          margin-bottom: 10px;\n\n          padding-left: 12px;\n          height: 26px;\n\n          display: flex;\n          align-items: center;\n\n          color: $white;\n\n          & .icon {\n            margin-right: 12px;\n            width: 26px;\n            height: 26px;\n            background-repeat: no-repeat;\n            background-position: center;\n            background-size: auto;\n          }\n\n          & .icon_1 {\n            background-image: url(\"@icons/facebook_icon.svg\"),\n              url(\"@icons/Ellipse.svg\");\n          }\n\n          & .icon_2 {\n            background-image: url(\"@icons/twitter_icon.svg\"),\n              url(\"@icons/Ellipse.svg\");\n          }\n\n          & .icon_3 {\n            background-image: url(\"@icons/instagram_icon.svg\"),\n              url(\"@icons/Ellipse.svg\");\n          }\n\n          & .icon_4 {\n            background-image: url(\"@icons/youtube_icon.svg\"),\n              url(\"@icons/Ellipse.svg\");\n          }\n\n          & .text {\n            line-height: 26px;\n          }\n\n          &:last-child {\n            margin-bottom: 0;\n            padding-bottom: 0;\n          }\n        }\n      }\n    }\n\n    .footer__navigate {\n      & .nav {\n        margin: 0 auto;\n        width: 362px;\n        align-items: flex-end;\n        color: $white;\n      }\n    }\n\n    .footer__subscribe {\n      padding-left: 32px;\n      color: $white;\n\n      & .subtitle {\n        margin-bottom: 14px;\n        @extend %subtitle;\n      }\n\n      & form {\n        margin-bottom: 216px;\n        width: 100%;\n        height: 42px;\n\n        input {\n          margin-right: 10px;\n          padding-left: 10px;\n\n          width: 228px;\n          height: 100%;\n\n          border: 0.7px solid $orang;\n          border-radius: 2px;\n\n          outline: none;\n\n          color: inherit;\n\n          background-color: transparent;\n\n          &::placeholder {\n            color: inherit;\n          }\n        }\n\n        button {\n          width: 129px;\n          height: 42px;\n\n          border: 1px solid #4b9200;\n          border-radius: 5px;\n\n          background-color: $light_gray;\n          color: $green;\n\n          text-transform: none;\n        }\n      }\n\n      p {\n        &:nth-child(3) {\n          margin-bottom: 10px;\n          text-align: right;\n\n          font-weight: 100;\n          line-height: 130%;\n        }\n      }\n    }\n  }\n}\n","@import \"@styles/fonts.scss\";\n@import \"@styles/variables.scss\";\n@import \"@styles/mixins.scss\";\n@import \"@styles/placeholders.scss\";\n\n@import \"@components/buttons.scss\";\n@import \"@components/watch/navigate.scss\";\n@import \"@components/watch/header.scss\";\n@import \"@components/watch/watch.scss\";\n@import \"@components/backstage.scss\";\n@import \"@components/animalsCard.scss\";\n@import \"@components/friend.scss\";\n@import \"@components/testimonials.scss\";\n@import \"@components/footer.scss\";\n\n* {\n  box-sizing: border-box;\n}\n\nbody {\n  @extend %main_text;\n  min-height: 100vh;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nbutton {\n  margin: 0;\n  padding: 0;\n}\n\nh2 {\n  @extend %h2_title;\n}\n\nh3 {\n  @extend %h3_title;\n}\n\nul,\nli {\n  margin: 0;\n  padding: 0;\n}\n\nli {\n  list-style: none;\n}\n\na {\n  text-decoration: none;\n  color: inherit;\n  &:hover {\n    cursor: pointer;\n  }\n}\n\nbutton {\n  @extend %buttons_text;\n  border: none;\n  text-transform: uppercase;\n}\n\n.logo {\n  display: block;\n  width: 82px;\n  height: 52px;\n  border: 0.5px solid $white;\n\n  font-size: 19.0145px;\n  line-height: 120%;\n  color: $white;\n\n  text-align: center;\n\n  background-image: url(\"@icons/bamboo.svg\");\n  background-repeat: no-repeat;\n  background-position: 10px 20px;\n}\n\n.container {\n  margin: 0 auto;\n  max-width: 1158px;\n}\n"],"sourceRoot":""}]);
